import $ from 'jquery';
import m from "../../prime-src/prepare.js";
import { useState } from 'react';

export default function FormLogIn() {
	const [idType, setIdType] = useState('id');
	const [userId, setUserId] = useState('');
	const [userPwd, setUserPwd] = useState('');
	const [rememberMe, setRememberMe] = useState('yes');
	const [logInDisabled, setLogInDisabled] = useState(false);
	const [logInErrs, setLogInErrs] = useState([]);
	const [forgotPwdDisabled, setForgotPwdDisabled] = useState(false);
	const [forgotPwdErrs, setForgotPwdErrs] = useState([]);

	m.logIn = function () {
		setLogInDisabled(true);
		let userIdLength = m.getUTF8Length(userId);
		let valid = false;

		if (userIdLength === 0) {
			setLogInErrs((errs) => [...errs, "[--ID or E-mail is empty.--]"]);
		}
		else if (userIdLength < m.minIdLength) {
			setLogInErrs((errs) => [...errs, "[--ID or E-mail is too short.--]"]);
		}
		else if (userPwd.length === 0) {
			setLogInErrs((errs) => [...errs, "[--Password is empty.--]"]);
		}
		else if (userPwd.length < m.minPwdLength) {
			setLogInErrs((errs) => [...errs, "[--Password is too short.--]"]);
		}
		else {
			if (m.regExId.test(userId)) {
				setIdType("id"); valid = true;
			}
			else if (m.regExEmail.test(userId)) {
				setIdType("email"); valid = true;
			}
			else {
				setIdType("invalid");
				setLogInErrs((errs) => [...errs, "[--ID--]/[--E-mail--] '" + userId + "' [--is of invalid form.--]"]);
			}
		}

		if (valid) {
			let form_data = `log	iehack	screenWidth	screenHeight	idType	userId	rememberMe	userPwd\nweb	&#9760;	${m.sW}	${m.sH}	${idType}	${userId}	${rememberMe}	`;
			setLogInErrs((errs) => [...errs, "[--Checking ID/E-mail--]: [--Please wait.--]"]);
			$.ajax("/account/pwd_iteration", {
				type: "POST"
				, data: idType + "\t" + userId
			}).fail(function () {
				setLogInErrs((errs) => [...errs, "[--Request times out.--] [--Please click the Log-in button again.--]"]);
				setLogInDisabled(false);
			}).done(function (resp) { // resp: pwd_iteration or "No such id"	salt
				let res = resp.split('\t');
				let iter = Number(res[0]);
				if (isNaN(iter)) {
					setLogInErrs((errs) => [...errs, "Error: " + res[0]]);
					setLogInDisabled(false);
				}
				else {
					setLogInErrs((errs) => [...errs, "[--Password is being encrypted.--]"]);
					setTimeout(function () {
						form_data += m.encrypt(res[1], userPwd, iter); setUserPwd("");
						setLogInErrs((errs) => [...errs, "[--Logging in--]: [--Please wait.--]"]);
						$.ajax("/account/log-in.do", {
							type: "POST"
							, data: form_data
						}).done(function (resp) {
							// gtag('event', 'conversion', { 'send_to': 'AW-11407455434/R4DjCJSIzPwYEMrpv78q', 'value': 0.1, 'currency': 'USD' });
							setLogInErrs((errs) => [...errs, resp]);
							// wait until saving session/rmb cookies.
							setTimeout(function () {
								if (resp.startsWith("log-in success")) {
									if (m.searchVars.goto?.val) {
										window.location.href = m.searchVars.goto.val;
									}
									else {
										window.location.pathname = "/";
									}
								}
								else {
									setLogInDisabled(false);
								}
							}, m.wait);
						});
					}, 8);
				}
			});
		}
		else {
			setLogInDisabled(false);
		}
	};

	m.forgotPwd = function () {
		setForgotPwdDisabled(true);
		let userIdLength = m.getUTF8Length(userId);
		let valid = false;

		if (userIdLength === 0) {
			setForgotPwdErrs((errs) => [...errs, "[--ID or E-mail is empty.--]"]);
		}
		else if (userIdLength < m.minIdLength) {
			setForgotPwdErrs((errs) => [...errs, "[--ID or E-mail is too short.--]"]);
		}
		else {
			if (m.regExId.test(userId)) {
				valid = true;
			}
			else if (m.regExEmail.test(userId)) {
				valid = true;
			}
			else {
				setForgotPwdErrs((errs) => [...errs, "[--ID--]/[--E-mail--] '" + userId + "' [--is of invalid form.--]"]);
			}
		}

		if (valid) {
			let form_data = "log\tiehack\tscreenWidth\tscreenHeight\tidType\tuserId\n"
				+ "web\t&#9760;\t" + m.sW + "\t" + m.sH + "\t" + idType + "\t" + userId.trim();
			setForgotPwdErrs((errs) => [...errs, "[--Checking ID/E-mail--]: [--Please wait.--]"]);
			$.ajax("/account/pwd_iteration", {
				type: "POST"
				, data: idType + "\t" + userId
			}).fail(function () {
				setForgotPwdErrs((errs) => [...errs, "[--Request times out.--] [--Please click the Forgot password button again.--]"]);
				setForgotPwdDisabled(false);
			}).done(function (resp) { // resp: pwd_iteration or "No such id"	salt
				let res = resp.split('\t');
				let iter = Number(res[0]);
				if (isNaN(iter)) {
					setForgotPwdErrs((errs) => [...errs, "Error: " + res[0]]);
					setForgotPwdDisabled(false);
				}
				else {
					setForgotPwdErrs((errs) => [...errs, "[--Sending an email--]: [--Please wait.--]"]);
					$.ajax("/account/forgotPwd", {
						type: "POST"
						, data: form_data
					}).done(function (resp) {
						setForgotPwdErrs((errs) => [...errs, resp]);
						setForgotPwdDisabled(false);
					});
				}
			});
		}
		else {
			setForgotPwdDisabled(false);
		}
	};

	return (
		<form id="form-log-in" className="form-login">
			<input id="input-idType" type="hidden" name="idType" value={idType} />
			<label htmlFor="input-userId">[--ID--] or [--E-mail--]</label>
			<input id="input-userId" className="user-id go-next" name="userId"
				value={userId} onChange={(e) => {
					setUserId(e.target.value.trim());
					if (m.regExId.test(e.target.value.trim())) {
						setIdType("id");
					}
					else if (m.regExEmail.test(e.target.value.trim())) {
						setIdType("email");
					}
					else {
						setIdType("invalid");
					}
				}}
				placeholder="[--ID--] or [--E-mail--]" type="text" minLength="1" maxLength="255" tabIndex="1" />
			<label htmlFor="input-userPwd">[--Password--]</label>
			<input id="input-userPwd" className="user-pwd" name="userPwd"
				value={userPwd} onChange={(e) => setUserPwd(e.target.value)}
				placeholder="[--Password--]" type="password" minLength="6" maxLength="255" tabIndex="2" />
			<label className="checkbox"><input id="checkbox" className="checkbox" name="rememberMe"
				value={rememberMe} onChange={(e) => setRememberMe(e.target.checked ? 'yes' : 'no')}
				type="checkbox" tabIndex="3" checked />{' '}[--Remember me--]</label>
			{logInErrs.filter((errMsg, i) => (i >= logInErrs.length - m.maxErrMsgs)).map((errMsg, i) => <div key={i} className="error-msg">{errMsg}</div>)}
			<button id="button-log-in" type="button" onClick={() => m.logIn()} tabIndex="4" disabled={logInDisabled}>[--Log in--]</button>
			{forgotPwdErrs.filter((errMsg, i) => (i >= forgotPwdErrs.length - m.maxErrMsgs)).map((errMsg, i) => <div key={i} className="error-msg">{errMsg}</div>)}
			<button id="button-forgot-pwd" type="button" onClick={() => m.forgotPwd()} tabIndex="5" disabled={forgotPwdDisabled}>[--Forgot password--]</button>
		</form>
	);
}
