import $ from 'jquery';
import m from "../../prime-src/prepare.js";
import { useState } from 'react';

export default function FormSignUp() {
	const [userId, setUserId] = useState('');
	const [userEmail, setUserEmail] = useState('');
	const [userPwd, setUserPwd] = useState('');
	const [userPwdCfm, setUserPwdCfm] = useState('');
	const [signUpDisabled, setSignUpDisabled] = useState(false);
	const [signUpErrs, setSignUpErrs] = useState([]);

	m.signUp = function () {
		setSignUpDisabled(true);
		let userIdLength = m.getUTF8Length(userId);
		let valid = false;

		if (userIdLength === 0) {
			setSignUpErrs((errs) => [...errs, "[--ID is empty.--]"]);
		}
		else if (userIdLength < m.minIdLength || userIdLength > m.maxIdLength) {
			setSignUpErrs((errs) => [...errs, `${(userIdLength < m.minIdLength ? "[--ID is too short.--]" : "[--ID is too long.--]")}`]);
			setSignUpErrs((errs) => [...errs, `'${userId}':${userIdLength} ([--ID--].[--length--]: ${m.minIdLength}~${m.maxIdLength})`]);
		}
		else if (!m.regExId.test(userId)) {
			setSignUpErrs((errs) => [...errs, "[--ID--] '" + userId + "' [--is of invalid form.--]"]);
		}
		else if (userEmail.length === 0) {
			setSignUpErrs((errs) => [...errs, "[--E-mail is empty.--]"]);
		}
		else if (!m.regExEmail.test(userEmail)) {
			setSignUpErrs((errs) => [...errs, "[--E-mail--] '" + userEmail + "' [--is of invalid form.--]"]);
		}
		else if (userPwd.length === 0) {
			setSignUpErrs((errs) => [...errs, "[--Password is empty.--]"]);
		}
		else if (userPwd.length < m.minPwdLength || userPwd.length > m.maxPwdLength) {
			setSignUpErrs((errs) => [...errs, `${(userPwd.length < m.minPwdLength ? "[--Password is too short.--]" : "[--Password is too long.--]")}`]);
			setSignUpErrs((errs) => [...errs, `([--Password--].[--length--]=${userPwd.length}: ${m.minPwdLength}~${m.maxPwdLength})`]);
		}
		else if (userPwd !== userPwdCfm) {
			setSignUpErrs((errs) => [...errs, "[--Confirm your password correctly.--]"]);
		}
		else {
			valid = true;
		}

		if (valid) {
			setSignUpErrs((errs) => [...errs, "[--Checking ID and E-mail--]: [--Please wait.--]"]);
			$.ajax("/account/check", {
				type: "POST"
				, data: userId + "\t" + userEmail
			}).fail(function () {
				setSignUpErrs((errs) => [...errs, "[--Request time out.--] [--Please click the Sign-up button again.--]"]);
				setSignUpDisabled(false);
			}).done(function (resp) { // resp: idAvailable	emailAvailable	tToken	authToken(char(64))
				let res = resp.split('\t');
				if (res && res.length > 2 && res[0] === "true" && res[1] === "true") {
					setSignUpErrs((errs) => [...errs, "[--Password is being encrypted.--]"]);
					setTimeout(function () {
						setUserPwd(m.encrypt(res[3], userPwd, m.iterFull));
						setUserPwdCfm("confirmed");
						$("#input-su-userPwd")[0].value = "";
						$("#input-su-userPwdCfm")[0].value = "";
						setSignUpErrs((errs) => [...errs, "[--Signing up--]: [--Please wait.--]"]);
						let form_data = `userId	userEmail	tToken	authToken	userPwd	userPwdCfm
${userId}	${userEmail}	${res[2]}	${res[3]}	${userPwd}	${userPwdCfm}`;
						setUserPwd(null); setUserPwdCfm(null);
						$.ajax("/account/sign-up", {
							type: "POST", data: form_data
						}).done(function (resp) {
							// gtag('event', 'conversion', { 'send_to': 'AW-11407455434/R4DjCJSIzPwYEMrpv78q', 'value': 0.1, 'currency': 'USD' });
							setSignUpErrs((errs) => [...errs, resp]);
							setSignUpDisabled(false);
						});
					}, 2048);
				}
				else {
					if (res[0] === "false") {
						setSignUpErrs((errs) => [...errs, `[--ID--] '${userId}' [--is already in use. Please try another ID.--]`]);
					}
					if (res[1] === "false") {
						setSignUpErrs((errs) => [...errs, `[--E-mail--] '${userEmail}' [--is already registered. Please try another e-mail.--]`]);
					}
					setSignUpDisabled(false);
				}
			});
		}
		else {
			setSignUpDisabled(false);
		}
	};

	return (
		<form id="form-sign-up" className="form-login" action="/account/sign-up" acceptCharset="UTF-8" method="post">
			<h2>[--Sign up for Recoeve--]</h2>
			<label htmlFor="input-su-userId">[--ID--]</label>
			<input id="input-su-userId" className="user-id go-next" name="userId"
				value={userId} onChange={(e) => setUserId(m.regExId.test(e.target.value.trim()) ? e.target.value.trim() : userId)}
				placeholder="[--ID--]" type="text" minLength="1" maxLength="255" tabIndex="7" />
			<label htmlFor="input-su-userEmail">[--E-mail--]</label>
			<input id="input-su-userEmail" className="user-Email go-next" name="userEmail" value={userEmail} onChange={(e) => setUserEmail(e.target.value)}
				placeholder="[--E-mail--]" type="text" tabIndex="8" />
			<label htmlFor="input-su-userPwd">[--Password--]</label>
			<input id="input-su-userPwd" className="user-pwd go-next" name="userPwd" value={userPwd} onChange={(e) => setUserPwd(e.target.value)}
				placeholder="[--Password--]" type="password" minLength="6" maxLength="255" tabIndex="9" />
			<label htmlFor="input-su-userPwdCfm">[--Password confirm--]</label>
			<input id="input-su-userPwdCfm" className="user-pwd" name="userPwdCfm" value={userPwdCfm} onChange={(e) => setUserPwdCfm(e.target.value)}
				placeholder="[--Password confirm--]" type="password" minLength="6" maxLength="255" tabIndex="10" />
			{signUpErrs.filter((errMsg, i) => (i >= signUpErrs.length - m.maxErrMsgs)).map((errMsg, i) => <div key={i} className="error-msg">{errMsg}</div>)}
			<button id="button-sign-up" type="button" onClick={() => m.signUp()} tabIndex="11" disabled={signUpDisabled}>[--Sign up for Recoeve--]</button>
		</form>
	);
}