import $ from 'jquery';
import m from "../prime-src/prepare.js";
import { useEffect } from 'react';
import FormLogIn from './components/FormLogIn.jsx';
import FormLogInWithGoogle from './components/FormLogInWithGoogle.jsx';
import FormSignUp from './components/FormSignUp.jsx';

m.searchVars = m.getSearchVars(window.location.search);
if (m.searchVars.lang !== undefined) {
	m.docCookies.setItem("lang", m.searchVars.lang.val, Infinity, "/");
}

m.encloseErr = function (text) {
	return `<div class="error-msg">${text}</div>`;
}

m.$document.ready(function () {
	setTimeout(function () {
		let blogStat = `URI\treferer\tREACTION_GUEST\n${window.location.href}\t${document.referrer}\tlog-in guest`;
		$.ajax({
			type: "POST", url: "https://recoeve.net/BlogStat", data: blogStat, dataType: "text"
		}).fail(function (resp) {
			console.log("BlogStat: ", resp);
		}).done(function (resp) {
			console.log("BlogStat: ", resp);
		});
	}, 8 * m.wait)
});

export default function LogIn() {
	async function initialEffect() {
		m.$button_log_in = $("#button-log-in");
		m.$button_forgot_pwd = $("#button-forgot-pwd");
		m.$log_in_with_google = $("#log-in-with-google");
		m.$button_sign_up = $("#button-sign-up");
		m.$input_su_userId = $("#input-su-userId");

		$(".replace-reco-eve").html(function (i, orgHtml) {
			return orgHtml
				.replace(/reco/ig, '<span class="reco bold">$&</span>')
				.replace(/eve/ig, '<span class="eve bold">$&</span>');
		});

		let $dataLang = $("#data-lang");
		let dataLang = await m.strToJSON($dataLang.html().trim());
		let htmlLang = `<div class="p">`;
		for (let i = 0; i < dataLang[0].length; i++) {
			let link;
			if (window.location.search.length > 1) {
				let searchVars = m.getSearchVars(window.location.search);
				if (searchVars['lang']) {
					searchVars['lang'].val = dataLang[0][i];
				}
				else {
					searchVars['lang'] = { key: 'lang', val: dataLang[0][i] };
					searchVars.push(searchVars['lang']);
				}
				link = `${ window.location.pathname }${ m.arrayToSearch(searchVars) }${ window.location.hash }`;
				htmlLang += `<a href="${link}" onclick="m.docCookies.setItem('lang', '${dataLang[0][i]}', Infinity, '/');">${dataLang[1][i]}</a><br />`;
			}
			else {
				link = `${window.location.pathname}?lang=${dataLang[0][i]}${window.location.hash}`;
				htmlLang += `<a href="${link}" onclick="m.docCookies.setItem('lang', '${dataLang[0][i]}', Infinity, '/');">${dataLang[1][i]}</a><br />`;
			}
		}
		htmlLang += `</div>`;
		$dataLang.after(htmlLang);

		$("#input-userPwd").on("keydown", function (e) {
			if (e.code === "Enter" || e.code === "NumpadEnter") { // Enter
				e.preventDefault();
				e.stopPropagation();
				m.$button_log_in.trigger("click");
			}
		});
		$("#input-userPwd").on("blur", function (e) {
			setTimeout(function () {
				m.$button_log_in.trigger("click");
			}, m.wait);
		});
		$("#input-su-wGoogle-userId").on("keydown", function (e) {
			if (e.code === "Enter" || e.code === "NumpadEnter") { // Enter
				e.preventDefault();
				e.stopPropagation();
				m.$log_in_with_google.trigger("click");
			}
		});
		$("#input-su-userPwdCfm").on("keydown", function (e) {
			if (e.code === "Enter" || e.code === "NumpadEnter") { // Enter
				e.preventDefault();
				e.stopPropagation();
				m.$button_sign_up.trigger("click");
			}
		});
		// TODO: Press <code>Enter</code> to go to the next input.
		$("input.go-next").on("keydown", function (e) {
			if (e.code === "Enter" || e.code === "NumpadEnter") { // Enter
				e.preventDefault();
				e.stopPropagation();
				$(e.target).next("input").trigger("focus");
			}
		});

		////////////////////////////////////////
		// Try Log-in or Sign-up with google.
		////////////////////////////////////////
		if (m.initialHash) {
			let hashSplit = m.initialHash.substring(1).replace(/&amp;/ig, "&").replace(/\+/g, "%20").split("&");
			for (let i = 0; i < hashSplit.length; i++) {
				let hashISplit = hashSplit[i].split("=");
				if (hashISplit.length >= 2) {
					m.hashMap[hashISplit[0]] = { key: hashISplit[0], val: hashISplit[1] };
				}
				else {
					m.hashMap[hashISplit[0]] = { key: hashISplit[0], val: "" };
				}
				m.hashMap.push(m.hashMap[hashISplit[0]]);
			}
			if (m.hashMap.state?.val && m.hashMap.access_token?.val && m.hashMap.token_type?.val) {
				m.$log_in_with_google[0].disabled = true;
				$.ajax({
					type: "GET", url: `https://www.googleapis.com/oauth2/v2/userinfo?access_token=${m.hashMap.access_token.val}&token_type=${m.hashMap.token_type.val}`
					, dataType: "json"
				}).done(function (resp) {
					console.log("Google data: ", resp);
					if (resp.verified_email && resp.email) {
						// Log-in with this email or Sign-up with this email and ID of sign-up, but state and ip check also.
						let dataToBeSent = `log\tiehack\tscreenWidth\tscreenHeight\tstate\temail\nweb\t&#9760;\t${m.sW}\t${m.sH}\t${m.hashMap.state.val}\t${resp.email}`;
						console.log("Do POST to google.do!: ", dataToBeSent);
						$.ajax({
							type: "POST", url: `/account/log-in/with/google.do`, data: dataToBeSent
							, dataType: "text"
						}).fail(function (resp) {
							console.log("fail: ", resp);
						}).done(async function (resp) {
							console.log("resp: ", resp);
							let res = resp.substring(15);
							res = await m.strToJSON(res);
							console.log("res: ", res);
							if (resp.startsWith("log-in success") && m.hashMap.state.val === String(res[1]?.state)) {
								// gtag('event', 'conversion', { 'send_to': 'AW-11407455434/R4DjCJSIzPwYEMrpv78q', 'value': 0.1, 'currency': 'USD' });
								m.$log_in_with_google.before(m.encloseErr("log-in success"));
								// wait until saving session/rmb cookies. And conversion.
								setTimeout(function () {
									if (res[1]?.goto) {
										window.location.hash = "";
										window.location.search = "";
										window.location.href = res[1].goto;
										// console.log(res[1].goto);
										// window.open(unescape(res[1].goto), "_blank");
									}
									else if (m.searchVars.goto?.val) {
										window.location.href = m.searchVars.goto.val;
									}
									else {
										window.location.href = "/";
									}
								}, m.wait);
							}
							else {
								m.$log_in_with_google.before(m.encloseErr(resp));
							}
						});
					}
					m.$log_in_with_google[0].disabled = false;
				});
			}
		}
		m.$delayedElems = $("[delayed-src], [delayed-bgimage], .to-be-executed");
	};

	useEffect(() => {
		initialEffect();
	}, []);

	return (
		<div id="recoeve-container">
			<h1>[--welcome--]</h1>
			<FormLogIn></FormLogIn>
			<FormLogInWithGoogle></FormLogInWithGoogle>
			<FormSignUp></FormSignUp>
			<div className="desc">
				<div className="p replace-reco-eve">[--desc 0--]</div>
				<div className="p replace-reco-eve">[--desc 1--]</div>
				<div className="p replace-reco-eve">[--desc 2--]</div>
				<div className="p replace-reco-eve">[--desc 3: Recoeve.net in one sentence--]</div>
			</div>
			<div className="desc">
				<div className="p">
					For the usage examples, visit my page (예제를 보시려면 제 페이지를 방문해 주세요.)
					<ul>
						<li><a target="_blank"
							href="/user/kipid/mode/multireco?cat=%5BMusic%2FBreak%5D--K-Pop#headPlay">
							[Music/Break]--K-Pop of kipid's Recoeve.net (Multireco mode)</a></li>
						<li><a target="_blank"
							href="/user/kipid/mode/multireco?cat=%5BMusic%2FBreak%5D--Pop#headPlay">
							[Music/Break]--Pop of kipid's Recoeve.net (Multireco mode)</a></li>
						<li><a target="_blank"
							href="/user/kipid/mode/multireco?cat=%5BMusic%2FBreak%5D--Pet#numbers-of-recos">
							[Music/Break]--Pet of kipid's Recoeve.net (Multireco mode)</a></li>
						<li><a target="_blank"
							href="/user/kipid/mode/multireco?cat=%5BMusic%2FBreak%5D--%EB%82%A8%EA%B7%9C%EB%A6%AC#headPlay">
							[Music/Break]--남규리 | Nam Gyuri (Actor of Korea) of kipid's Recoeve.net (Multireco mode)</a></li>
						<li><a target="_blank"
							href="https://recoeve.net/user/kipid?cat=%5BPhysics%2FMath%2FScience%5D--Physics#headPlay">
							[Physics/Math/Science]--Physics of kipid's Recoeve.net</a></li>
						<li><a target="_blank"
							href="https://recoeve.net/user/kipid?cat=%5B%EC%9D%8C%EC%8B%9D%2F%EC%9A%94%EB%A6%AC%2F%EA%B1%B4%EA%B0%95%5D--%EA%B1%B4%EA%B0%95#headPlay">
							[음식/요리/건강]--건강 of kipid's Recoeve.net</a></li>
					</ul>
				</div>
			</div>

			<div className="lang">
				<div className="p">Language/언어/語言</div>
				<data id="data-lang">{`en	ko	zh	ja	de	fr	es	hi	ar	pt	ru
English/영어/英語/en	Korean/한국어/韓語/ko	Chinese/중국어(간체)/中文/zh	Japanese/일본어/日語/ja	deutsche Sprache/독일어/Deutsch/de	Français/French/프랑스어/佛語/fr	Español/Spanish/스페인어/西班牙語/es	हिन्दी/Hindi/힌디어/印地语/hi	اللغة العربية/ar	Português/Portuguese/포르투갈어/葡萄牙語/pt	Русский/Russian/러시아어/俄語/ru`}</data>
			</div>

			<div id="foot">[--version--]{' '}
				<a target="_blank" href="https://recoeve.net/">Recoeve.net</a>
				[--foot 0--]
				<a target="_blank" href="[--foot 1: Feedback link--]">[--foot 1: Feedback name--]</a>
				[--foot 2: ending--]<br />
				<a target="_blank" href="https://kipid.tistory.com/entry/Recoeve-net-3S-Slow-Sexy-Sincere-SNS-%EC%82%AC%EC%9A%A9%EC%84%A4%EB%AA%85%EC%84%9C-Manual">
					Introducing what we are making: Recoeve.net (3S|Slow/Sexy/Sincere SNS) 사용설명서/Manual</a>
			</div>
			<div className="p">Recoeve.net Manual collection | 사용방법 모음집: <a className="wheat" target="_blank"
				href="https://recoeve.net/user/kipid?cat=%5BRecoeve%5D--Manual%2F%EC%84%A4%EB%AA%85%EC%84%9C#headPlay">
				[Recoeve]--Manual/설명서 of kipid's Recoeve.net</a></div>
			<div className="caption p cmt">How to use Recoeve.net and How to reco (레코이브 사용법)</div>
			<div className="p rC">
				<div className="rSC">
					<iframe delayed-src="https://www.youtube.com/embed/zfdpwhGlY7s"></iframe>
				</div>
			</div>
		</div>
	);
}
